import axios from "axios"
import { isEmpty } from "../../fe/Array"
import { dayMs, minuteMs } from "../../fe/Date"
import { defer } from "../../fe/Defer"
import { fmtDuration } from "../../fe/FmtDuration"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { gte } from "../../fe/Number"
import { SimpleAppName } from "../AppName"
import { EditionType } from "../EditionName"
import { mkLogger } from "../Logger"
import { osFullName } from "../OperatingSystem"
import { version } from "../Version"
import { configDir } from "../dir/ConfigDir"
import { PosixFile } from "../fs/PosixFile"
import { isDirectory } from "../fs/Stat"
import { HealthCheckTest } from "../health/HealthCheck"
import { t } from "../licensing/Licensing"
import { Settings } from "../settings/Settings"
import { channel } from "./PhotoStructureVersion"
import { UpdateChannelVersion } from "./UpdateChannelVersion"
import { UpdateChannels } from "./UpdateChannels"

const logger = defer(() => mkLogger("VersionCheck"))

export function currentChannel() {
  const least = [Settings.updateChannel.valueOrDefault, channel()]
    .map(ea => UpdateChannels.ordinal(ea))
    .sort()[0]
  // we want the least-stable value:
  return UpdateChannels.values[least] ?? Settings.updateChannel.valueOrDefault
}

export async function checkVersion_(): Promise<
  HealthCheckTest & {
    latestVersion?: string
  }
> {
  if (!Settings.autoUpdateCheck.valueOrDefault) {
    const msg = ["PhotoStructure version checking is disabled"]
    if (Settings.optOut.valueOrDefault === true) {
      msg.push("Set " + Settings.optOut.toEnvLine(false) + " to enable.")
    }
    if (!Settings.autoUpdateCheck.valueOrDefault) {
      msg.push(
        "Set " + Settings.autoUpdateCheck.toEnvLine(true) + " to enable."
      )
    }
    return { level: "ok", msg }
  }

  const dir = configDir()
  if (dir == null || !(await isDirectory(dir))) {
    return {
      level: "warn",
      msg: [
        "PhotoStructure version checking is disabled",
        "No suitable configuration directory exists."
      ]
    }
  }

  const channelVersions = await getChannelVersions_()
  if (channelVersions == null) {
    return {
      level: "warn",
      msg: ["Failed to fetch PhotoStructure version information"]
    }
  }

  const current = currentChannel()
  const latestVersion = channelVersions.versions.find(
    ea => ea.channel === current
  )?.version

  if (latestVersion == null) {
    return {
      level: "warn",
      msg: [
        "PhotoStructure version checking failed",
        "No suitable version was found for " +
          Settings.updateChannel.toEnvLine()
      ]
    }
  }

  if (latestVersion === version) {
    const agoMs = Date.now() - channelVersions.updatedAt
    const lastCheckedMsg =
      agoMs < minuteMs ? "just now" : fmtDuration(agoMs, 1) + " ago"
    return {
      level: "ok",
      msg: [
        "PhotoStructure is up to date",
        "You are running the latest version of PhotoStructure (" +
          version +
          ")",
        "Last checked " + lastCheckedMsg
      ],
      latestVersion
    }
  } else {
    return {
      level: "warn",
      msg: [
        "PhotoStructure is out of date",
        "You are running version " +
          version +
          ", but " +
          latestVersion +
          " is available"
      ],
      latestVersion
    }
  }
}
export async function userAgent() {
  return Settings.allowUserAgent.valueOrDefault
    ? systemUserAgent()
    : SimpleAppName
}

export async function systemUserAgent() {
  return (
    SimpleAppName +
    "/" +
    version +
    " (" +
    osFullName() +
    ") " +
    EditionType().toLowerCase() +
    "/" +
    (await t())
  )
}

interface ChannelVersionJson {
  versions: UpdateChannelVersion[]
  updatedAt: number
}

const ChannelVersionsUrl = "https://photostructure.com/channel-versions.json"

export async function getChannelVersions_(): PromiseMaybe<ChannelVersionJson> {
  if (!Settings.autoUpdateCheck.valueOrDefault) return
  const versionFile = PosixFile.for(configDir()!).join("channel-versions.json")
  const priorJson = await versionFile.readJson<ChannelVersionJson>()
  const expiresAt = Date.now() - dayMs
  if (priorJson != null && gte(priorJson.updatedAt, expiresAt)) {
    return priorJson
  }

  const UserAgent = await userAgent()

  const response = await axios.get(ChannelVersionsUrl, {
    headers: { "User-Agent": UserAgent }
  })
  const result = {
    versions: response.data,
    updatedAt: Date.now()
  }
  if (isEmpty(result.versions)) {
    throw new Error(
      "Invalid response from " + ChannelVersionsUrl + ": empty response.data"
    )
  }

  logger().info("Fetched version metadata", {
    url: ChannelVersionsUrl,
    UserAgent,
    result
  })

  await versionFile.writeJson_(result)
  return result
}
