import { hourMs } from "../../fe/Date"
import { defer } from "../../fe/Defer"
import { checkVersion_ } from "../version/VersionCheck"
import { HealthCheck } from "./HealthCheck"

export const versionHealthCheck = defer(() =>
  HealthCheck.for({
    section: "System",
    id: "ps-version",
    pendingMsg: "Checking PhotoStructure version",
    settings: ["autoUpdateCheck", "configDir"],
    ttlMs: hourMs,
    rejectIsWarn: true,
    links: [
      {
        text: "Read more about PhotoStructure updates",
        icon: "docs",
        url: "https://forum.photostructure.com/t/new-in-v2023-10-automated-photostructure-version-checking/1956"
      }
    ],
    later: checkVersion_
  })
)
